export default {
  namespaced: true,
  state: {
    token: null,
    user: null,
    email: '',
    password: '',
    remember: false,
    expirationDate: null,
  },
  getters: {},
  mutations: {
    setCredentials(state, credentials) {
      state.user = credentials.user
      state.token = credentials.token
      state.remember = credentials.remember
      state.email = credentials.email
      state.password = credentials.password
      state.expirationDate = credentials.expirationDate
    },
    refreshToken(state, credentials) {
      state.token = credentials.token
      state.expirationDate = credentials.expirationDate
    },
    clearCredentials(state, credentials) {
      state.user = credentials.user
      state.token = credentials.token
      state.expirationDate = credentials.expirationDate
    },
  },
  actions: {
    setCredentials(context, payload) {
      context.commit('setCredentials', payload)
    },
    refreshToken(context, payload) {
      context.commit('refreshToken', payload)
    },
    logout(context) {
      context.commit('clearCredentials', { user: null, token: null, expirationDate: null })
    },
  },
}
