import Vue from 'vue'
import VueRouter from 'vue-router'

import store from '@/store/index'
import auth from '@/middlewares/auth'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior() {
    return { x: 0, y: 0 }
  },
  routes: [
    {
      path: '/inicio',
      name: 'home',
      component: () => import('@/views/home/HomePage.vue'),
      meta: {
        pageTitle: 'Seguimiento de Vehículos',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Home',
            active: true,
          },
        ],
      },
    },
    {
      path: '/marcas',
      name: 'brands-page',
      component: () => import('@/views/brands/BrandsPage.vue'),
      meta: {
        pageTitle: 'Marcas',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Listado de Marcas',
            active: true,
          },
        ],
      },
    },
    {
      path: '/modelos',
      name: 'models-page',
      component: () => import('@/views/models/ModelsPage.vue'),
      meta: {
        pageTitle: 'Modelos',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Listado de Modelos',
            active: true,
          },
        ],
      },
    },
    {
      path: '/tipo-dispositivos',
      name: 'type-devices-page',
      component: () => import('@/views/type-devices/TypeDevicesPage.vue'),
      meta: {
        pageTitle: 'Tipos de dispositivos',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Listado de Tipos de dispositivos',
            active: true,
          },
        ],
      },
    },
    {
      path: '/dispositivos',
      name: 'devices-page',
      component: () => import('@/views/devices/DevicesPage.vue'),
      meta: {
        pageTitle: 'Dispositivos',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Listado de Dispositivos',
            active: true,
          },
        ],
      },
    },
    {
      path: '/consuctores',
      name: 'drivers-page',
      component: () => import('@/views/drivers/DriversPage.vue'),
      meta: {
        pageTitle: 'Conductores',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Listado de Conductores',
            active: true,
          },
        ],
      },
    },
    {
      path: '/unidades',
      name: 'units-page',
      component: () => import('@/views/units/UnitsPage.vue'),
      meta: {
        pageTitle: 'Unidades',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Listado de Unidades',
            active: true,
          },
        ],
      },
    },
    {
      path: '/grupos',
      name: 'teams-page',
      component: () => import('@/views/teams/TeamsPage.vue'),
      meta: {
        pageTitle: 'Grupos',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Listado de Grupos',
            active: true,
          },
        ],
      },
    },
    {
      path: '/grupo/:id',
      name: 'team-page',
      component: () => import('@/views/teams/TeamUnitsPage.vue'),
      meta: {
        pageTitle: 'Grupo',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Grupos',
            to: { name: 'teams-page' },
          },
          {
            text: 'Grupo',
            active: true,
          },
        ],
      },
    },
    {
      path: '/alertas',
      name: 'alerts-page',
      component: () => import('@/views/alerts/AlertsPage.vue'),
      meta: {
        pageTitle: 'Alertas',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Listado de Alertas',
            active: true,
          },
        ],
      },
    },
    {
      path: '/monitoreo',
      name: 'trackings-page',
      component: () => import('@/views/trackings/TrackingsPage.vue'),
      meta: {
        pageTitle: 'Monitoreo',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Monitoreo de Vehículos',
            active: true,
          },
        ],
      },
    },
    {
      path: '/bitacora-monitoreo',
      name: 'binnacle-tracking-page',
      component: () => import('@/views/binnacle-tracking/BinnacleTrackingsPage.vue'),
      meta: {
        pageTitle: 'Bitácora',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Bitácora de Monitoreo',
            active: true,
          },
        ],
      },
    },
    {
      path: '/usuarios',
      name: 'users-page',
      component: () => import('@/views/users/UsersPage.vue'),
      meta: {
        pageTitle: 'Usuarios',
        middleware: [auth],
        breadcrumb: [
          {
            text: 'Listado de usuarios',
            active: true,
          },
        ],
      },
    },
    {
      path: '/perfil',
      name: 'profile-page',
      component: () => import('@/views/users/ProfilePage.vue'),
      meta: {
        pageTitle: 'Mi perfil',
        middleware: [auth],
        breadcrumb: [
          {
            text: '',
            active: true,
          },
        ],
      },
    },
    {
      path: '/',
      name: 'login',
      component: () => import('@/views/login/Login.vue'),
      meta: {
        layout: 'full',
      },
    },
    {
      path: '/error-404',
      name: 'error-404',
      component: () => import('@/views/error/Error404.vue'),
      meta: {
        layout: 'full',
      },
    },
    {
      path: '*',
      redirect: 'error-404',
    },
  ],
})

router.beforeEach((to, from, next) => {
  const { token } = store.state.auth
  if (token && to.name === 'login') {
    return next({ name: 'home' })
  }
  return next()
})

export default router
