import axios from 'axios'
import store from '@/store'

const instance = axios.create({
  baseURL: `${process.env.VUE_APP_BASE_URL}/api`,
})

// Interceptor de solicitud
instance.interceptors.request.use(
  config => {
    // Obtener el token de donde lo tengas almacenado (por ejemplo, localStorage)
    // const token = localStorage.getItem('token') // Cambia esto según donde almacenes tu token
    const token = store.state.auth?.token
    // Si hay un token, lo agregamos al header de la solicitud
    if (token) {
      // eslint-disable-next-line no-param-reassign, dot-notation
      config.headers['Authorization'] = `Bearer ${token}`
    }
    return config// Asegúrate de retornar la configuración modificada
  },
  error => {
    console.log('ERROR....', { error })
  },
)

// Interceptor de respuesta
instance.interceptors.response.use(
  response => response,
  async error => {
    const originalRequest = error.config

    // Verificar si el error es por token expirado (suponiendo que el código de error es 401)
    // eslint-disable-next-line no-underscore-dangle
    if (error.response && error.response.status === 401 && !originalRequest._retry) {
      // eslint-disable-next-line no-underscore-dangle
      originalRequest._retry = true // Marcar la solicitud original para evitar bucles infinitos
    }

    return Promise.reject(error) // Si no es un error de token expirado, rechazar el error
  },
)

export default instance
