import Vue from 'vue'
import Vuex from 'vuex'

import createPersistedState from 'vuex-persistedstate'
// import * as Cookies from 'js-cookie'
// Modules
import app from './app'
import appConfig from './app-config'
import verticalMenu from './vertical-menu'
import auth from './auth'

Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState({
    key: 'trackpro',
    storage: {
      getItem: key => localStorage.getItem(key), // Obtener del localStorage
      setItem: (key, value) => localStorage.setItem(key, value), // Guardar en localStorage
      removeItem: key => localStorage.removeItem(key), // Eliminar de localStorage
    },
  })],
  modules: {
    app,
    appConfig,
    verticalMenu,
    auth,
  },
  strict: process.env.DEV,
})
