/* eslint-disable no-restricted-globals */
/* eslint-disable global-require */
import { ref, reactive, computed } from '@vue/composition-api'

import AuthService from '@/services/AuthService'
import store from '@/store/index'
import { localize } from 'vee-validate'

export default ctx => {
  const isLogging = ref(false)
  const isError = ref(false)
  const messageError = ref('')
  const locale = () => {
    localize({
      en: {
        messages: {
          // generic rule messages...
        },
        fields: {
          email: {
            required: 'El email es obligatorio',
            // email:''
          },
          password: {
            required: 'La contraseña es obligatoria!',
          },
        },
      },
    })
  }
  // eslint-disable-next-line global-require
  const sideImg = ref(require('@/assets/images/pages/login-v2.svg'))

  const form = reactive({
    email: store.state.auth.email,
    password: store.state.auth.password,
    remember: store.state.auth.remember,
  })

  const rememberme = ref(false)

  const imgUrl = computed(() => {
    // eslint-disable-next-line global-require
    let img = require('@/assets/images/pages/login-v2.svg')
    if (store.state.appConfig.layout.skin === 'dark') {
      img = require('@/assets/images/pages/login-v2-dark.svg')
    }
    return img
  })

  const passwordToggleIcon = computed(() => (ctx.parent.$children[0].passwordFieldType === 'password' ? 'EyeOffIcon' : 'EyeIcon'))

  const parseJwt = token => {
    const base64Url = token.split('.')[1] // Obtener la parte del cuerpo
    const base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/') // Reemplazar caracteres de Base64Url
    const jsonPayload = decodeURIComponent(escape(window.atob(base64))) // Decodificar Base64
    return JSON.parse(jsonPayload) // Convertir a objeto JSON
  }

  const signIn = () => {
    const { remember, ...rest } = form
    isLogging.value = true
    AuthService.login(rest)
      .then(response => {
        const { user, token } = response.data
        const decoded = parseJwt(token)
        const expirationTime = decoded.exp // Obtener el tiempo de expiración
        const expirationDate = new Date(expirationTime * 1000) // Convertir a milisegundos
        store.dispatch('auth/setCredentials', {
          user,
          token,
          email: remember ? form.email : '',
          password: remember ? form.password : '',
          remember,
          expirationDate,
        })
        isLogging.value = false
        location.reload()
      })
      .catch(error => {
        const { message, response } = error

        if (message) if (message === 'Network Error') messageError.value = 'No se pudo conectar al servidor'
        if (response) messageError.value = 'Usuario o contraseña incorrectos'

        isLogging.value = false
        isError.value = true
      })
  }

  const revalidateToken = () => {
    isLogging.value = true
    AuthService.revalidateToken()
      .then(response => {
        const { token } = response.data
        const decoded = parseJwt(token)
        const expirationTime = decoded.exp // Obtener el tiempo de expiración
        const expirationDate = new Date(expirationTime * 1000) // Convertir a milisegundos
        store.dispatch('auth/refreshToken', {
          token,
          expirationDate,
        })
      })
      .catch(error => {
        console.log({ error })
      })
  }

  const validationForm = () => {
    ctx.refs.loginValidation.validate().then(success => {
      if (success) signIn()
    })
  }

  locale()

  return {
    sideImg,
    form,
    rememberme,
    imgUrl,
    passwordToggleIcon,
    validationForm,
    isLogging,
    isError,
    messageError,
    revalidateToken,
  }
}
