import Api from '@/services/Api'

const resource = 'v1/auth'

export default {
  login(form) {
    return Api.post(`${resource}/login`, form)
  },
  logout(token) {
    return Api.get(`${resource}/login`, {
      params: { token },
    })
  },
  revalidateToken() {
    return Api.get(`${resource}/revalidate-token`)
  },
}
